* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: sans-serif;
}

.nav-list {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav-links {
  text-decoration: none;
  color: black;
  font-size: 18px;
}

.home,
.about,
.contact {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}

.contact {
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

.home-content,
.about-content {
  padding-top: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  img {
    height: 62%;
  }

  h1 {
    font-size: 50px;
    margin: 30px 0px;
  }

  p {
    font-size: 20px;
  }
}

.contact-content {
  padding-top: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  .mapa {
    margin-top: 80px;
  }
}
